





import { Vue, Component, Prop, Mixins } from "vue-property-decorator";
import JobEditor from "@/components/misc/JobEditor.vue";
import { Overwrite } from "@/shared";
import { jobsModule, routerModule } from "@/store/modules";
import { IJob } from "@/store/modules/JobsModule";

@Component({
  components: {
    JobEditor
  }
})
export default class NewJobView extends Mixins() {
  get jobId(): string {
    return routerModule.params.jobId;
  }

  get jobData() {
    return jobsModule.getById(this.jobId);
  }

  async onSubmit(job: IJob) {
    // TODO: infer args from other class
    // TODO: trycatch
    const resp = await jobsModule.updateJob(job);
    if (resp && resp.payload) {
      this.$router.replace({
        path: `/jobs/${resp.payload.job.id}`
      });
    }
  }
}
